import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/esm/Row';

export const FrontPage = () => {
    return (
        <Container fluid className='bg-pattern font-std'>
            <Row>
                <h1>Row 1</h1>
            </Row>
            <Row>
                <h1>Row 2</h1>
            </Row>
        </Container>
    );
};
